a {
    text-decoration: none;
    color: black;
}

.btn{
    color: white;
    background-color: tomato;
    border-radius: 10px;
    font-size: 20px;
    margin-right: 10px;
    cursor: pointer;
}

.li{
 list-style: none;   
 margin-top: 10px;
}

li:hover a{
    font-weight: bold;
    border-bottom: 1px solid tomato;
}


.img{
    margin-left: 10px;
    margin-right: 10px;
    height: 50px;
    vertical-align: middle;
}

.h2{
    text-align: left;
}


.h2:hover span a{
    color: azure;
}

.title{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.titleSvg{
    height: 30px;
    margin-right: 15px;
    cursor: pointer;
}

.textarea{
    width: 60%;
    height: 300px;
    border-radius: 15px;
    font-size: larger;
}
.textarea:hover{
    border: 4px solid coral;
}

.result{
    margin-top: 15px;
    width: 60%;
    background-color: gray;
    border-radius: 15px;
}

.popup{
    position: absolute;
    width: 200px;
    height: 200px;
    right: 0px;
    background-color: cyan;
    border-radius: 10px;
    animation: fadein 5s ease 1s;
    -webkit-animation: fadein 1s; /* Safari and Chrome */
}

@keyframes fadein {
    from {
        opacity: 0;
    }
    to {
        opacity: 0.6;
    }
}

@-webkit-keyframes fadein { /* Safari and Chrome */
    from {
        opacity: 0;
    }
    to {
        opacity: 0.6;
    }
}